$accent-color: #fe3e57;
$default-color: #8d8d8d;
$title-color: #29282d;
$bg: #f6f6f6;

%quicksand-400 {
    font-family: 'Quicksand-Regular', sans-serif;
    font-weight: 400;
}

%quicksand-500 {
    font-family: 'Quicksand-Medium', sans-serif;
    font-weight: 500;
}

%roboto-400 {
    font-family: 'Roboto-Regular', sans-serif;
    font-weight: 400;
}

%roboto-500 {
    font-family: 'Roboto-Medium', sans-serif;
    font-weight: 500;
}

%roboto-700 {
    font-family: 'Roboto-Bold', sans-serif;
    font-weight: 700;
}

%accent-btn {
    color: #fff;
    border: none;
    background-color: $accent-color;
    text-transform: uppercase;
    box-shadow: 0px 7px 20px 0px rgba(255, 17, 48, 0.3);
    cursor: pointer;
}

%checkbox-hide {
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
    position: absolute;
}