  transition: all 0.2s;
}
.person__social-link path:hover {
  fill: #fe3e57;
}

.filter-latest__post {
  display: flex;
  padding-bottom: 15px;
  margin-bottom: 20px;
  border-bottom: 1px solid #ececec;
}
.filter-latest__post:hover .filter-latest__title {
  color: #fe3e57;
}
.filter-latest__post:last-child {
  border: none;
  padding-bottom: 0;
  margin-bottom: 0;
}
.filter-latest__content {
  margin-left: 20px;
}
.filter-latest__title {
  font-size: 17px;
  margin-bottom: 5px;
  transition: all 0.2s;
  color: #29282d;
}
.filter-latest__date {
  position: relative;
  padding-left: 30px;
}
.filter-latest__date::before {
  content: "";
  position: absolute;
  left: 0;
  top: 2px;
  width: 16px;
  height: 16px;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url(../../img/icons/calendar-accent.svg);
}

@media (max-width: 1100px) {
  .blog-page__item-box {
    padding: 25px;
  }
}
@media (max-width: 992px) {
  .blog-page__inner {
    flex-direction: column-reverse;
  }
  .blog-page__aside {
    width: 100%;
  }
  .blog-page__aside .filter-search__input {
    width: 100%;
  }
  .blog-page__content {
    width: 100%;
    margin-right: 0;
  }
  .blog-page__img {
    width: 100%;
    height: 450px;
  }
  .blog-page .person,
  .blog-page .filter-latest,
  .blog-page .filter-category,
  .blog-page .filter-popular {
    display: none;
  }
}
@media (max-width: 620px) {
  .blog-page__img {
    height: 300px;
  }
  .blog-page__item .tns-controls button[data-controls=next] {
    top: 130px;
  }
  .blog-page__item .tns-controls button {
    top: 140px;
  }
}
.blog-one__post {
  margin-bottom: 25px;
}
.blog-one__content-box {
  padding: 30px;
  border: 1px solid #ececec;
  border-top: none;
}
.blog-one__params {
  margin-bottom: 30px;
}
.blog-one__params span {
  padding-left: 25px;
  position: relative;
}
.blog-one__params span::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  background-repeat: no-repeat;
  width: 20px;
  height: 20px;
}
.blog-one__date::before {
  background-image: url(../../img/icons/calendar-accent.svg);
}
.blog-one__comments {
  margin: 0 35px;
}
.blog-one__comments::before {
  background-image: url(../../img/icons/comments-accent.svg);
}
.blog-one__author::before {
  background-image: url(../../img/icons/user-accent.svg);
}
.blog-one__title {
  font-size: 22px;
  color: #29282d;
  margin-bottom: 25px;
}
.blog-one__blockquote {
  margin: 30px 0;
  padding: 40px;
  background-color: #f6f6f6;
  border-left: 3px solid #fe3e57;
  font-size: 18px;
  color: #29282d;
}
.blog-one__inner {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}
.blog-one__inner img {
  margin-right: 30px;
}
.blog-one__techs {
  display: flex;
  justify-content: space-between;
  padding: 0 25px;
}
.blog-one__techs-tags {
  text-transform: uppercase;
  color: #29282d;
}
.blog-one__techs-tags span {
  text-transform: capitalize;
  color: #8d8d8d;
}
.blog-one__techs-socials {
  text-transform: uppercase;
  color: #29282d;
}
.blog-one__techs-socials a {
  margin-left: 10px;
}
.blog-one__techs-socials a path {
  fill: #8d8d8d;
  transition: all 0.2s;
}
.blog-one__techs-socials a path:hover {
  fill: #fe3e57;
}
.blog-one__employee, .blog-one__reviews {
  padding: 40px 0;
  margin-bottom: 35px;
  border-bottom: 1px solid #ececec;
}
.blog-one__employee {
  display: flex;
}
.blog-one__employee img {
  margin-right: 30px;
}
.blog-one__employee-content {
  padding: 15px 0;
}
.blog-one__employee-name {
  color: #29282d;
  text-transform: uppercase;
  font-size: 22px;
  margin-bottom: 20px;
}
.blog-one__reviews-title, .blog-one__form-title {
  font-size: 24px;
  color: #29282d;
  margin-bottom: 30px;
  text-transform: uppercase;
}
.blog-one__form-inputs {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}
.blog-one__form-input {
  padding: 15px 20px;
  width: 375px;
  border: 1px solid #ececec;
}
.blog-one__form-textarea {
  width: 100%;
  resize: none;
  border: 1px solid #ececec;
  padding: 15px 20px;
  height: 200px;
  margin-bottom: 30px;
}
.blog-one__form-btn {
  padding: 12px 25px;
}

@media (max-width: 992px) {
  .blog-one__post-image {
    width: 100%;
  }
}
@media (max-width: 768px) {
  .blog-one__inner img {
    height: fit-content;
