  .blog-one__content-box {
    padding: 20px;
  }
  .blog-one__form-inputs {
    display: block;
    margin-bottom: 0;
  }
  .blog-one__form-input {
    width: 100%;
    margin-bottom: 20px;
  }
}
@media (max-width: 620px) {
  .blog-one {
    margin: 70px 0;
  }
  .blog-one__inner {
    display: block;
  }
  .blog-one__inner img {
    width: 100%;
    height: 300px;
    margin-bottom: 20px;
  }
}
@media (max-width: 550px) {
  .blog-one__params span {
    display: block;
    margin-bottom: 10px;
  }
  .blog-one__blockquote {
    padding: 20px;
  }
  .blog-one__comments {
    margin: 0 0 10px;
  }
  .blog-one__employee {
    display: block;
    padding: 25px 0;
    margin-bottom: 25px;
  }
  .blog-one__employee img {
    margin-right: 0;
    width: 100%;
    height: 250px;
  }
  .product-review__content {
    margin-left: 15px;
  }
}
@media (max-width: 450px) {
  .blog-one__techs {
    display: block;
  }
  .blog-one__techs-tags {
    margin-bottom: 10px;
  }
