.contacts {
  margin: 110px 0;
}
.contacts__map iframe {
  width: 100%;
  height: 450px;
}
.contacts__inner {
  display: flex;
  justify-content: space-between;
  margin-top: 110px;
}
.contacts__content {
  max-width: 365px;
}
.contacts__title {
  color: #29282d;
  font-size: 47px;
  margin-bottom: 30px;
  line-height: 58px;
}
.contacts__text {
  margin-bottom: 50px;
}
.contacts__techs {
  max-width: 300px;
  color: #29282d;
}
.contacts__techs div {
  position: relative;
  padding-left: 80px;
}
.contacts__techs div::before {
  position: absolute;
  content: "";
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 5px 20px 0px rgba(146, 146, 146, 0.3);
  top: -5px;
  left: 0;
  background-repeat: no-repeat;
  object-fit: cover;
  background-position: center center;
}
.contacts__techs div + div {
  margin-top: 40px;
}
.contacts__phones a {
  display: inline-block;
}
.contacts__phones::before {
  background-image: url(../../img/icons/phone-accent.svg);
}
.contacts__adress::before {
  background-image: url(../../img/icons/location-accent.svg);
}
.contacts__emails::before {
  background-image: url(../../img/icons/email-accent.svg);
}
.contacts__form {
  padding: 60px 40px;
  box-shadow: 0px 10px 40px 0px rgba(172, 172, 172, 0.3);
  max-width: 770px;
}
.contacts__form-title {
  color: #29282d;
  font-size: 22px;
  margin-bottom: 20px;
}
.contacts__form-inputs {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
  gap: 30px;
}
.contacts__form-input {
  border: none;
  background-color: #f6f6f6;
  padding: 12px 20px;
  width: 100%;
  min-width: 330px;
}
.contacts__form-textarea {
  width: 100%;
  margin: 30px 0;
  border: none;
  background-color: #f6f6f6;
  padding: 12px 20px;
  resize: none;
  height: 200px;
}
.contacts__form-btn {
  padding: 12px 20px;
}

@media (max-width: 1100px) {
  .contacts__inner {
    display: block;
    margin-top: 70px;
  }
  .contacts__content {
    margin-bottom: 30px;
  }
  .contacts__content, .contacts__form {
    max-width: 100%;
    width: 100%;
  }
}
@media (max-width: 830px) {
  .contacts__form {
    padding: 40px;
  }
  .contacts__form-inputs {
    flex-direction: column;
  }
}
@media (max-width: 500px) {
  .contacts {
    margin: 70px 0;
  }
  .contacts__map iframe {
    height: 250px;
  }
  .contacts__title {
    font-size: 32px;
    line-height: 40px;
  }
  .contacts__form {
    padding: 25px;
  }
  .contacts__form-input {
    min-width: auto;
  }
}